<template>
  <v-navigation-drawer app :permanent="permanent">
    <div class="d-flex justify-center my-3">
      <div class="text-center">
        <div class="d-block">
          <img src="@/assets/logo.svg" width="100%" />   
        </div>
      </div>
    </div>

    <v-list
      dense
      nav>
      <!-- <v-subheader class="font-weight-bold">MENU UTAMA</v-subheader> -->
      <v-list-item-group v-model="list" color="purple">
        <template
          v-for="item in items">
          <v-list-item
            v-if="item.link !== 'children'"
            :key="item.title"
            link 
            @click="item.link === ''?keluar():$router.push({name: item.link})">
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          
          <v-list-group
            v-else
            :key="item.title"
            no-action
            :value="false">
            <template v-slot:activator>
              <v-list-item-icon>
                <v-icon>{{ item.icon }}</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>{{ item.title }}</v-list-item-title>
              </v-list-item-content>
            </template>

            <v-list-item
              v-for="(admin, i) in item.children"
              :key="i"
              link
              @click="$router.push({name: admin.link})">              
              <v-list-item-content>
                <v-list-item-title>{{ admin.title }}</v-list-item-title>
              </v-list-item-content>
              <v-list-item-icon>
                <v-icon>{{ admin.icon }}</v-icon>
              </v-list-item-icon>
            </v-list-item>
          </v-list-group> 
        </template>
      </v-list-item-group>      
    </v-list>
  </v-navigation-drawer>	
</template>

<script>
	export default {
		props: ['permanent'],

		data: () => ({
			list: 0,
			items: [
        { title: 'Dashboard', icon: 'mdi-view-dashboard', link: 'admin' },
        { title: 'Daftar incubator', icon: 'mdi-ballot', link: 'daftar-incubator' },
        { title: 'Daftar startup', icon: 'mdi-spa', link: 'daftar-startup' },
        { 
          title: 'Data Master', 
          icon: 'mdi-database', 
          link: 'children',
          children: [
            {
              title: 'Data Bank', 
              icon: 'mdi-credit-card-outline', 
              link: 'data-bank',
            }
          ]
        },
        { title: 'Keluar', icon: 'mdi-exit-to-app', link: '' }
      ]
		}),

    methods: {
      keluar () {
        window.localStorage.removeItem('token_abp')
        this.$router.push({name: 'login-admin'})
      }
    }
	}
</script>