<template>
	<v-app-bar app elevation="3" color="white">
    <v-app-bar-nav-icon 
      class="d-flex d-lg-none mr-3"
      @click.stop="sidebar"
    ></v-app-bar-nav-icon>
    <v-chip 
      label 
      class="grey lighten-4 purple--text">
      {{ title[$route.name] }}
    </v-chip>
    <v-spacer></v-spacer>
    <v-col 
      cols="12" sm="4" 
      class="pa-0">
      <v-autocomplete
        placeholder="search for people, places, and things"
        dense
        solo flat
        prepend-inner-icon="mdi-magnify"
        class="mt-6 search"
      ></v-autocomplete>
    </v-col>
  </v-app-bar>
</template>

<style>
  .search.theme--light.v-text-field--solo > .v-input__control > .v-input__slot {
    background: #f8f8f8;
  }
</style>

<script>
  export default{
    props: ['permanent'],

    data: () => ({
      title: {
        'admin': 'Dashboard',
        'daftar-incubator': 'Daftar Incubator',
        'daftar-startup': 'Daftar Startup'
      }
    }),

    methods: {
      sidebar() {
        this.$emit('editPermanent', !this.permanent)
      }
    },

    watch: {
      selectedStartup(val) {
        this.$store.dispatch('updateStartup', val)
      }
    }
  }
</script>
